<template>
	<div id="news-index">
		<public-header title=""></public-header>
		<div class="content">
			<div class="categorys-wrap van-hairline--bottom">
				<div class="cate-active" v-if="categorys[id]">{{categorys[id]['title']}}</div>
				<div class="cate-active" v-else></div>
				<div class="cate-all swiper-container" id="cate-swiper">
					<ul class="swiper-wrapper">
						<li class="cate-item ellipsis swiper-slide" v-for="item in categorys" :key="'c' + item.id" :data-id="item.id">{{item.title}}</li>
					</ul>
				</div>
			</div>
			<van-list
				v-model="records.loading"
				:finished="records.finished"
				@load="onLoad"
				:offset="100"
				:immediate-check="false"
				v-if="!records.empty"
				class="article-list"
			>
				<div class="article-head">
					<div class="title"></div>
				</div>
				<div class="article-item" v-for="(item, index) in records.data" :key="item.id">
					<router-link :to="util.getUrl({path: '/pages/news/news/detail', query:{id: item.id}})">
						<div class="article-wrap flex-lr van-hairline--top">
							<div class="article-left">
								<div class="text">{{item.title}}</div>
								<div class="read"> <span>{{item.click}}</span></div>
							</div>
							<div class="article-img">
								<img :src="item.thumb" alt="">
							</div>
						</div>
					</router-link>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips"></div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>!</p>
			</div>
			<iloading v-if="showPreLoading"></iloading>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getRecords} from "@/controller/funcCommon"
	import Swiper from 'swiper';
	import 'swiper/dist/css/swiper.min.css';
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				records:{
					page: 1,
					psize: 8,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				categorys: {},
				showPreLoading: true,
			}
		},
		methods: {
			onLoad(force, swiperStatus){
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'manage/news/news/list',
					data: {
						cid: that.id
					},
					success: (res) => {
						that.categorys = res.categorys;
						if(swiperStatus) {
							that.$nextTick(function() {
								that.swiper = new Swiper('.swiper-container', {
									autoplay: {
										delay: 2000,
										stopOnLastSlide: false,
										disableOnInteraction: false,
									},
									loop: true,
									slidesPerView: 4,
									on: {
										click: function(e) {
											//thisswiper
											let id = e.target.dataset.id;
											that.$router.push(that.util.getUrl({path: '/pages/news/news/index', query: {id: id}}));
										}
									}
								})
							})
						}
					}
				});
			},
		},
		created() {
			if(this.$route.query) {
				this.id = this.$route.query.id;
			}
		},
		watch: {
			'$route' (to, from) {
				this.showPreLoading = true;
				if(this.$route.query && this.$route.query.id > 0) {
					this.id = this.$route.query.id
				}
				//swiper
				//this.swiper.destroy();
				this.onLoad(true);
			}
		},
		mounted(){
			this.onLoad(false, true);
		}
	}
</script>

<style>
	#news-index .content .article-list{
		padding-top: 10px;
	}
	#news-index .article-head{
		padding-left: 15px;
		background-color: #fff;
	}
	#news-index .article-head .title{
		padding: 10px 15px 10px 0;
	}
	#news-index .article-item{
		padding-left: 10px;
		background-color: #fff;
	}
	#news-index .article-item .article-wrap{
		color: #333;
		padding: 15px 10px 15px 0;
	}
	#news-index .article-item .article-wrap .article-left{
		padding-right: 10px;
		flex: 1;
		word-wrap: break-word;
	}
	#news-index .article-item .article-wrap .article-left .text{
		font-size: 16px;
		font-weight: bold;
		line-height: 22px;
		height: 44px;
	}
	#news-index .article-item .article-wrap .article-left .read{
		padding-top: 10px;
		font-size: 14px;
		color: #999;
	}
	#news-index .article-item .article-wrap .article-left .read span{
		padding-left: 3px;
	}
	#news-index .article-item .article-wrap .article-img{
		height: 68px;
		width: 40%;
	}
	#news-index .article-item .article-wrap .article-img img{
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	#news-index .categorys-wrap{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		background-color: #fff;
		font-size: 14px;
	}
	#news-index .categorys-wrap .cate-active{
		width: 20%;
		height: 46px;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #33aafc;
		position: relative;
	}
	#news-index .categorys-wrap .cate-active:before{
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 0;
		border-bottom: 2px solid #33aafc;
	}
	#news-index .categorys-wrap .cate-all{
		width: 80%;
		height: 46px;
	}
	#news-index .categorys-wrap .cate-all .cate-item{
		text-align: center;
		line-height: 46px;
		padding: 0 5px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
</style>


